* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #15191C;
  color: #ffffff;
  min-height: 100vh;
  line-height: 1.6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 70%;
  margin: 0 auto;
  padding: 40px 20px;
  position: relative;
  animation: fadeIn 0.6s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.header {
  text-align: center;
  margin-bottom: 60px;
}

.header h1 {
  font-size: 3.5rem;
  font-weight: 800;
  color: #C3FF19;
  margin-bottom: 16px;
  letter-spacing: -0.02em;
  animation: slideInDown 0.8s ease-out;
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.header p {
  color: #a0a0a0;
  font-size: 1.2rem;
  max-width: 500px;
  margin: 0 auto;
  font-weight: 400;
  animation: slideInUp 0.8s ease-out 0.2s both;
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.main-content {
  position: relative;
  display: flex;
  gap: 40px;
  align-items: flex-start;
}

.form-section {
  flex: 1;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 48px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  animation: slideInUp 0.8s ease-out 0.4s both;
  z-index: 2;
  position: relative;
}

.results-section {
  position: sticky;
  top: 40px;
  width: 300px;
  background: rgba(195, 255, 25, 0.1);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid rgba(195, 255, 25, 0.3);
  padding: 40px 30px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  animation: slideInUp 0.8s ease-out 0.6s both;
  z-index: 1;
  margin-top: 0;
  margin-left: -20px;
  align-self: flex-start;
}

.form-group {
  margin-bottom: 32px;
  animation: fadeInUp 0.6s ease-out;
}

.form-group:nth-child(1) { animation-delay: 0.6s; }
.form-group:nth-child(2) { animation-delay: 0.7s; }
.form-group:nth-child(3) { animation-delay: 0.8s; }
.form-group:nth-child(4) { animation-delay: 0.9s; }
.form-group:nth-child(5) { animation-delay: 1.0s; }
.form-group:nth-child(6) { animation-delay: 1.1s; }

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.form-label {
  display: block;
  margin-bottom: 12px;
  font-weight: 600;
  color: #ffffff;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  opacity: 0.9;
}

.form-select,
.form-input {
  width: 100%;
  padding: 16px 20px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  font-size: 16px;
  background: rgba(255, 255, 255, 0.05);
  color: #ffffff;
  transition: all 0.3s ease;
  outline: none;
  backdrop-filter: blur(10px);
}

.form-select:focus,
.form-input:focus {
  border-color: #C3FF19;
  box-shadow: 0 0 0 4px rgba(195, 255, 25, 0.1);
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

.form-select option {
  background: #1a1a1a;
  color: #ffffff;
}

.radio-group {
  display: flex;
  gap: 24px;
  margin-top: 12px;
}

.radio-item {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  padding: 12px 20px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.radio-item:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.radio-item input[type="radio"] {
  width: 20px;
  height: 20px;
  accent-color: #C3FF19;
}

.radio-item input[type="radio"]:checked + span {
  color: #C3FF19;
  font-weight: 600;
}

.button-group {
  display: flex;
  gap: 16px;
  margin-top: 40px;
  justify-content: flex-start;
  animation: fadeInUp 0.6s ease-out 1.2s both;
}

.btn {
  padding: 16px 32px;
  border: none;
  border-radius: 16px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  min-width: 140px;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: #C3FF19;
  color: #15191C;
  box-shadow: 0 8px 25px rgba(195, 255, 25, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(195, 255, 25, 0.4);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

.results {
  text-align: left;
}

.result-item {
  margin-bottom: 30px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.result-item:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.result-item:last-child {
  margin-bottom: 0;
}

.result-label {
  font-weight: 500;
  font-size: 0.9rem;
  color: #ffffff;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 8px;
  display: block;
}

.result-value {
  font-size: 2.5rem;
  font-weight: 700;
  color: #C3FF19;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: #a0a0a0;
  animation: fadeIn 0.6s ease-out;
}

.empty-state h3 {
  font-size: 1.5rem;
  margin-bottom: 12px;
  color: #ffffff;
}

.empty-state p {
  font-size: 1rem;
  opacity: 0.7;
}

@media (max-width: 1200px) {
  .container {
    width: 85%;
  }
  
  .main-content {
    flex-direction: column;
    gap: 20px;
  }
  
  .results-section {
    position: static;
    width: 100%;
    margin: 0;
    order: -1;
  }
}

@media (max-width: 768px) {
  .container {
    width: 95%;
    padding: 20px 16px;
  }
  
  .form-section {
    padding: 32px 24px;
  }
  
  .results-section {
    padding: 30px 20px;
  }
  
  .header h1 {
    font-size: 2.5rem;
  }
  
  .button-group {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .btn {
    width: 100%;
    max-width: 300px;
  }
  
  .radio-group {
    flex-direction: column;
    gap: 12px;
  }
  
  .result-value {
    font-size: 2rem;
  }
}
